.navbar {
  background: #6a1b9a;
  min-height: 70px;
  position: fixed;
  width: 100%;
  color: white;
  z-index: 99;
}

.navbar a {
  color: white;
  font-weight: 900;
  font-family: sans-serif;
}

.navbar li {
  display: inline-block;
  padding: 26px 0;
  width: 33.3%;
  text-align: center;
}



